<template>
  <div>
    <div class="header">
      <van-nav-bar
        title="登录"
        left-text="返回"
        right-text="注册"
        @click-left="$router.go(-1)"
        @click-right="go"
        left-arrow
      />
    </div>

    <div class="text">
      <h1>你好，</h1>
      <h1>欢迎来到茶语</h1>
    </div>

    <div class="login">
      <van-form>
        <van-field
          v-model="userInfo.phone"
          type="text"
          label="手机号"
          placeholder="输入11位手机号"
          left-icon="phone-o"
          maxlength="11"
          :rules="[]"
        />

        <van-field
          v-model="userInfo.password"
          :type="isbool ? 'text' : 'password'"
          label="密码"
          placeholder="密码至少包含数字和英文(6-20位)"
          left-icon="lock"
          :right-icon="isbool ? 'eye-o' : 'closed-eye'"
          @click-right-icon="viewPassword"
          maxlength="16"
          :rules="[]"
        />
        <div class="login-btn">
          <van-button round block type="info" native-type="submit" @click="login">点击登录</van-button>
        </div>
      </van-form>
      <div class="tips clearfix">
        <p class="fr" @click="forGet">找回密码</p>
      </div>
    </div>

    <div class="ways">
      <van-divider>第三方登录</van-divider>
      <van-row>
        <van-col span="8">
          <span class="iconfont icon-qq"></span>
        </van-col>
        <van-col span="8">
          <span class="iconfont icon-weixin"></span>
        </van-col>
        <van-col span="8">
          <span class="iconfont icon-tubiaozhizuo-"></span>
        </van-col>
      </van-row>
    </div>
  </div>
</template>

<script>
import Testing from "../assets/js/testing";
export default {
  data() {
    return {
      userInfo: {
        phone: "",
        password: ""
      },
      isbool: true
    };
  },
  methods: {
    login() {
      if (Testing.Testing(this.userInfo, this)) {
       
        let data = Object.assign({}, this.userInfo);
        data.appkey = this.appkey;
        

        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration: 0
        });

        this.axios({
          method: "post",
          url: "/login",
          //传统写法
          //  data:{
          //     phone: this.userInfo.phone,
          // nickName: this.userInfo.nickName,
          // password: this.userInfo.password,
          // appkey:'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA='
          //  }
          data
        })
          .then(res => {
            if (res.data.code == 200) {
              this.$toast.clear();
              // this.$toast(res.data.msg);

              //存储token，用于后面登录验证
              localStorage.setItem("__jigng", res.data.token);
              this.$router.push({ name: "推荐" });
            } else {
              this.$toast(res.data.msg);
            }
          })
          .catch(err => {
            
          });
      }
    },
    viewPassword() {
      this.isbool = !this.isbool;
    },
    go() {
      this.$router.push({ name: "注册" });
    },
    forGet() {
      this.$router.push({ name: "忘记" });
    }
  }
};
</script>

<style lang="less" scoped>
.text {
  width: 80%;
  margin: 0 auto;
  margin-top: 80px;
  margin-bottom: 50px;
  color: #263434;
  h1 {
    margin: 0;
    padding: 0;
    font-weight: bold;
    font-size: 28px;
  }
}
.login {
  width: 85%;
  margin: 0 auto;
  color: #505c5c;
}
.van-cell {
  font-weight: bolder;
}
.tips{
  width: 90%;
  margin: 0 auto;
}
.ways {
  width: 80%;
  margin: 0 auto;
  .van-col {
    text-align: center;
    .iconfont {
      display: block;
      color: #707070;
      font-size: 20px;
    }
  }
}
input {
  color: black;
}
</style>